.screen {
  width: 100%;
  height: 100%;
  display: flex;

  /* Misc */
  align-items: center;
  flex-direction: column;
  justify-content: center;
}
.terminalBar {
    height: 24px;
    width: 100%;
    background-color: #2c3645;
    display: flex;
    justify-content: flex-end;
}
.terminalBarButton {
    height: 24px;
    width: 32px;
    font-size: 14px;
    padding: 0px;
    margin: 0px;
    border: 0px;
    padding-left: 2px;
    background-color: #2c3645;
    opacity: 1;
    border: 0px solid #999999;
}
.terminalBarButton:disabled {
   opacity: 0.5;
   border: 1px solid #999999;
}

.editorIco{
    height: 22px;
    width: 32px;
    background: #f0f0f0;
    border: 0px;
}
.terminalBody {
    width: 100%;
    flex: 1;
    display: flex;
    max-height: calc(100vh - 50px)
}
.terminalExp {
    width: 100px;
    min-width: 150px;
    max-height: calc(100vh - 78px);
    background-color: #ffffff;
    border: 1px solid DarkGray;
    font-size: 12px;
    overflow: auto;
}
.terminalSplit{
    width: 6px;
    /*height: 100%;*/
    background-color: #2c3645;
}
.terminalSplit.vertical{
    width: 6px;
    cursor: ew-resize;
}
.terminalSplit.horizontal{
    height: 6px;
    width: 100%;
    cursor: ns-resize;
}
.terminalSplit:hover {
    background-color: blue;
}
.termeditor {
    flex: 1;
    display: flex;
    max-height: calc(100vh - 78px);
    flex-direction: column;
    justify-content: center;
}
.editorBody {
    height: 50%;
    background-color: #ffffff;
    display: flex;
    flex-direction: column;
    /* z-index: 0; */
    border-radius: 5%;
}
.editorBar{
    height: 24px;
    background-color: #f0f0f0;
    border-bottom: 1px #000000 solid;
    border-top: 1px #000000 solid;
    display: flex;
    justify-content: flex-start;
}
.editorBarTitle{
    display: flex;
    align-items: center;
    padding-left: 5px;
    font-size: 12px;    
    color: blue;
}
.editorBarButtons{
    flex: 1;
    display: flex;
    justify-content: flex-end;
}
.editor {
    flex: 1;
    background-color: #ffffff;
}
.terminalWindow {
    flex: 1;
    border: 2px #2d2e2c solid;
}
.terminal {
  height: 100%;
}

/* for explorer */
.caret {
  cursor: pointer;
  -webkit-user-select: none; /* Safari 3.1+ */
  -moz-user-select: none; /* Firefox 2+ */
  -ms-user-select: none; /* IE 10+ */
  user-select: none;
}

.caret::before {
  content: "\25B6";
  color: black;
  display: inline-block;
  margin-right: 6px;
}

.caret-down::before {
  -ms-transform: rotate(90deg); /* IE 9 */
  -webkit-transform: rotate(90deg); /* Safari */
  transform: rotate(90deg);
}

.nested {
  display: none;
}

.active {
  display: block;
}

.terminalExp ul li {
  padding-left: 4px;
}

.plain:hover {
  color: blue; 
  cursor: pointer;
}

.header {
  display: flex;
  flex-flow: row nowrap;
  background: #1e2530;
  margin: 0;
  justify-content:center;
  /*border: 2px solid #cbd5e0;*/
}

.header__left {
    /* Take the remaining width */
    width: 33%; 
    display: flex;
    flex-flow: row nowrap;
    justify-content:flex-start;
    align-items: center;
}

.header__middle {
    /* Take the remaining width */
    width: 33%;

    display: flex;
    flex-flow: row nowrap;
    justify-content:center;
    align-items: center;
    height: 40px;
}

.header__right {
    /* Take the remaining width */
    flex: 1;

    /* Misc */
    display: flex;
    align-items: center;
    flex-flow: row nowrap;
    justify-content: flex-end;
}


.navigation {

  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: flex-end;
  
  list-style: none;
  margin: 0; 
  background: #1e2530;
  height: 40px;
}

.navigation a {
  color: #ffffff!important; 
  padding-left: 20px;
  padding-right: 10px;
}


.navigation a:hover {
  color: #ff685f!important;
}


@media all and (max-width: 800px) {
  .navigation {
    justify-content: space-around;
  }
}

@media all and (max-width: 600px) {
  .navigation {
    flex-flow: column wrap;
    padding: 0;
  }
  .navigation a {
    text-align: center;
    padding: 10px;
    border-top: 1px solid rgba(255, 255, 255,0.3);
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }
  .navigation li:last-of-type a {
    border-bottom: none;
  }
}

.lesson_title {
	color: white;
}

/* for power button */
#power-text{
  color: #2a2a2a;
  text-shadow: 0 1px 0 #ffba92;
  text-align: center;
}

#btn-bg{
  width: 82px;
  height: 82px;
  background: #cdd3c9;
  border-radius: 41px;
  margin: 10px auto 20px;
  border-bottom: 2px solid #2a2a2a;
  border-top: 2px solid #fff;
  overflow: hidden;
  position: relative;
  cursor: pointer;
}

#btn-bg.active{
  background: #2d3036;
  border-bottom: 2px solid #ffcab6;
  border-top: 2px solid #e66847;
}

#btn-highlight{
  width: 41px;
  height: 82px;
  background: #eee;
  margin-left: 41px;
}

.active #btn-highlight{
  background: #52555d;
}

#btn-ring{
  position: absolute;
  width: 42px;
  height: 42px;
  border: 10px solid #2a2a2a;
  top: 14px;
  left: 20px;
  border-radius: 31px;
}

.active #btn-ring{
  border-color: #61fc8c;
}

#ring-line{
  width: 10px;
  height: 41px;
  margin: -14px auto;
  background: #2a2a2a;
  border-radius: 7px;
  border-right: 3px solid #eee;
  border-left: 3px solid #cdd3c9;
}

.active #ring-line{
  background: #61fc8c;
  border-right: 3px solid #52555d;
  border-left: 3px solid #2d3036;
}
/* end of power button */

/* start of the rightclick button */
.context-menu {
  position: absolute;
  text-align: left;
  background: white;
  border: 1px solid black;
  font-size: 11px;
  box-shadow: 5px 2px;
  z-index:100;
}

.context-menu ul {
  padding: 0px;
  margin: 0px;
  min-width: 10px;
  list-style: none;
}

.context-menu ul li {
  padding-left: 10px;
  padding-right: 50px;
  text-align: left;
  border-bottom: 1px solid gray;
}

.context-menu ul li a {
  text-decoration: none;
  color: black;
}

.context-menu ul li:hover {
  background: darkgray;
}
/* end of the rightclick button */

.inputBox {
  font-size: 11px;
  display: none;
}


@layer base {
    body {
      @apply font-poppins;
    }

    .containerBox {
      @apply flex h-[calc(100vh-94px)] w-auto;
      /* Misc */
      /* border: 2px solid #cbd5e0; */
      /* height: 100%;
      width: 100%; */
    }

    .container__left {
      @apply flex items-start w-full h-full p-4 overflow-y-scroll overflow-x-hidden
      /* Initially, the left takes 3/4 width */
      /* width: 50%; */

      /* Misc */
      /* align-items: center;
      display: flex;
      justify-content: center; */
    }
    .container__right {
      @apply flex h-full w-full overflow-hidden
      /* Misc */
      /* min-width: 400px;
      align-items: center;
      display: block; */
    }

    .lesson_button {
      @apply block bg-blue-400 hover:bg-blue-500 md:w-1/2 w-full duration-200 text-white px-4 py-2 rounded-3xl my-2
    }

    .lesson_high {
      @apply block bg-yellow-300 rounded-sm px-2 py-1 w-fit my-2
    }

}

